



















export default {
	data() {
		return {
			search_state:'',//需要携带至搜索详情的参数
			historyList:['羞羞的铁锤与我的故事','王八与铁锤的爱恨情仇','论铁锤的重要性与钢丝球','二笔说铁锤','王八说'],
			searchInfo:'',
		};
	},
	onLoad(options) {
		console.log(options.search_state);
		let _this = this;
		_this.startcreatview();
		_this.search_state = options.search_state;
	},
	methods:{
		// 跳转搜索详情
		toSearchDetails(){
			let _this = this;
			console.log('data-search:',_this.search_state);
			uni.navigateTo({
				url:'../searchContent/searchContent?search_state=' + _this.search_state
			})
		},
		// 点击历史记录
		clickHistory(info){
			console.log(info);
			this.searchInfo = info;
		},
		// 删除历史记录
		removeHistory(){
			this.historyList = [];
		},
		// 绘制搜索框
		startcreatview() {
			var s = this;
			s.TitleTimer = setTimeout(function() {
				s.createView();
			}, 100); //具体缓迟时间可以按需设置
		},
		createView() {
			clearTimeout(this.TitleTimer);
			var s = this;
			var pages = getCurrentPages();
			var page = pages[pages.length - 1];
			var currentWebview = page.$getAppWebview();
			var nTitle = currentWebview.getTitleNView();
			nTitle.draw([
				{
					tag: 'rect',
					id: 'rect',
					color: '#999',
					position: {
						left: '50px',
						right: '50px',
						top: '7px',
						bottom: '7px'
					},
					rectStyles: {
						color: '#fff',
						radius: '8px',
						borderColor: '#999'
					}
				},
				{
					tag: 'font',
					id: 'font',
					text: '\ue63e',
					position: {
						left: '55px',
						width: '30px',
						top: '7px',
						bottom: '7px'
					},
					textStyles: {
						size: '13px',
						fontSrc: '/static/iconfont.ttf',
						color: '#999',
					}
				},
				{
					tag: 'input',
					id: 'input',
					position: {
						left: '80px',
						right: '70px',
						top: '7px',
						bottom: '7px'
					},
					inputStyles: {
						placeholder: '关键字搜索',
						borderRadius: '30px',
						borderWidth: '0px',
						fontSize: '13px',
						type: 'search',
						fontSrc: '/static/iconfont.ttf',
						color: '#999',
						borderColor:'#999',
						onComplete: function(e) {
							console.log('点击搜索执行:' , JSON.stringify(e));
							s.searchInfo = e.text;
							console.log(s.search_state);
							s.toSearchDetails();
						},
						onFocus: function(e) {
							console.log('获得焦点:', JSON.stringify(e));
						},
						onBlur: function(e) {
							console.log('失去焦点:', JSON.stringify(e));
						},
					}
				}
			]);
		}
		// end
	},
	// 原生按钮点击事件
	onNavigationBarButtonTap(e) {
		console.info(e.index);
		if(e.text === '搜索'){
			console.log(this.searchInfo);
			this.toSearchDetails();
		}
	}
}
